/* css reset */
*{box-sizing:border-box;}
*:before,*after{box-sizing:border-box;}
*{margin:0;padding:0;}
ul,ol{list-style:none;}
a{color:inherit;text-decoration: none;}
img{max-width: 100%;max-height: 100%;}
/* style */
body{
background:#eee;
}
.searchForm{
    
    margin:20px;
    display:flex;
    justify-content:space-between;
    
}
@media(min-width){
    .searchForm{
        max-width: 400px;
    }
}
.searchForm > input{ 
    width:100%;
    margin-right:10px;
    height:40px;
    
    padding:0 10px;
    border-radius:4px;
}
.searchForm > button{ 
    white-space:nowrap;
    padding:0 28px;
    border:none;
    border-radius:4px;
    background: #100E76;
    color:white;
    font-size: 16px;
}
.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    
}
@media (min-width:500px){
    .globalHeader{
        margin:60px 0;
    }
}

.siteList{
    display:flex;
    flex-wrap:wrap;
    margin:0 20px;
  
}
@media (min-width:500px){
    .siteList{
        margin-left:0;
        margin-right:-25px;
    }
}
@media (min-width:500px){
    .searchForm{
        max-width:400px;
        margin-left:auto;
        margin-right: auto;
    }
}
.siteList > li{
    margin-bottom:10px;
    margin-right:6px;
    justify-content: space-between;
} 

@media(min-width:500px){
    .siteList > li{
    margin-right:25px;
    justify-content: flex-start;
    }
}

.siteList .site{
   
    width:160px;
    display:flex;
    justify-content: center;
    align-items:center;
    flex-direction:column;
    background:white;
    margin-bottom: 10px;
    border-radius: 4px;
    padding:20px 0;
    position:relative;
    cursor:pointer;
}

.siteList .site .close{
    position:absolute;
    right:0;
    top:0;
    display:none;
    cursor:default;
}
.siteList .site:hover > .close{
    display:block;
}

.siteList .site > .log{
    
    width:64px;
    height:64px;
    display:flex;
    justify-content: center;
    align-items:center;
    font-size: 64px;
}
.siteList .site > .link{
    font-size: 18px;
    margin-top:4px;
}
.siteList .addButton { 
    width:160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding:20px 0;
    background-color: white;
}
.siteList .addButton .icon{ 
    
    width:64px;
    height:64px;

}
.siteList .addButton .icon-Wrapper{ 
    width:64px;
    height:64px;
    display:flex;
    justify-content: center;
    align-items: center;
}
.siteList .addButton > .text{
    font-size: 18px;
    margin-top:4px;
}